import React from 'react'

function Firstimage(props) {
    return (
        <div className='ileri'>
          <img className='pancho'id='firstimage' src={props.image} alt={props.alt}></img> 
          <button className='btn btn-primary'><b>
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          {props.name}&nbsp;&nbsp;&nbsp;&nbsp;
          &nbsp;&nbsp;&nbsp;&nbsp;
          &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          </b></button> 
        </div>
    )
}

export default Firstimage
